import React from "react";

export function Locate({ panTo }) {
  return (
    <button
      className="locate"
      onClick={() =>
        navigator.geolocation.getCurrentPosition(
          (position) =>
            panTo({
              lat: position.coords.latitude,
              lng: position.coords.longitude,
            }),
          () => null,
          null
        )
      }
    >
      <img
        src="data:image/png;base64,"
        alt="compas - locate me"
      />
    </button>
  );
}
